
html, body, #root, #app, #app>div {
  height: 100%
}

.loading-div
{
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 10rem;
  height: 10rem;
  border-radius: 3px;
}
.stars {
	font-size: 1.5rem;
	font-style: normal;
	position: relative;
	display: block;
	top: -.1em;
}

.stars i {
	font-style: normal;
}

.full-star:after {
	content: "\2605";
}

.empty-star:after {
	content: "\2606"
}

.tab-content{
    border-left: 1px solid #ddd;
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
}

.color-picker span div span div {
	background-color: black;
}

[hidden] {
  display: none !important;
}


#profilePictureUpload:hover {
  cursor: pointer;
}

body, html {
    height: 100%;
    }
    .bg {
    background-image: url("https://amp.businessinsider.com/images/5a57df4ef421493e028b4752-1334-1001.jpg");
    height: 95vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    }

.cover {
    position: absolute;
    bottom: 1em;

}

.placeholder {
    height:50vh;
    background-color:black;
    opacity: 0;
    display: block;
  }

